<template>
  <div class="container text-center">
    <div class="row">
      <div class="bg-white m-5 mx-auto p-4 w-auto shadow rounded login-frm">
        <!-- Tabs Titles -->

        <!-- Icon -->
        <div class="fadeIn first">
          <img class="mb-2" style="width: 30px" src="../assets/logo.svg" />
          <h6 class="mb-2 fs-3 fw-bold text-center">ثبت نام رایگان</h6>
        </div>

        <!-- Login Form -->
        <div
          v-if="error"
          class="bg-danger fw-bold mb-4 px-4 py-2 rounded text-white"
        >
          {{ error }}
        </div>
        <form>
          <div v-if="step1" class="mb-3 text-start">
            <label for="first_name" class="form-label">نام</label>
            <input
              type="text"
              class="form-control rounded-0"
              id="first_name"
              v-model="first_name"
            />
          </div>

          <div v-if="step1" class="mb-3 text-start">
            <label for="last_name" class="form-label">نام خانوادگی</label>
            <input
              type="text"
              class="form-control rounded-0"
              id="last_name"
              v-model="last_name"
            />
          </div>

          <div v-if="step1" class="mb-3 text-start">
            <label for="login" class="form-label">تلفن همراه</label>
            <input
              type="text"
              class="form-control rounded-0"
              id="login"
              v-model="userPhone"
              placeholder="0912*******"
            />
          </div>

          <div v-if="step1" class="mb-3 text-start">
            <label for="login" class="form-label">کد معرف(اختیاری)</label>
            <input
              type="text"
              class="form-control rounded-0"
              id="referrer"
              dir="ltr"
              v-model="referrer"
              :readonly="referrerFromUrl"
            />
          </div>
          <div v-if="step2" class="mb-3 text-start">
            <label for="password" class="form-label">رمز عبور</label>

            <div class="input-group" lang="en">
              <span class="input-group-btn">
                <button
                  class="btn btn-secondary rounded-0"
                  type="button"
                  @click="togglePass"
                >
                  <span v-show="showEyeSlash === true">
                    <i class="fas fa-eye-slash"></i>
                  </span>
                  <span v-show="showEyeSlash === false">
                    <i class="fas fa-eye"></i>
                  </span>
                </button>
              </span>
              <input
                type="password"
                id="password"
                class="form-control"
                v-model="password"
                dir="ltr"
              />
            </div>
          </div>

          <div v-if="step2" class="mb-3 text-start">
            <label for="password-repeat" class="form-label"
              >تکرار رمز عبور</label
            >

            <div class="input-group" lang="fa">
              <span class="input-group-btn">
                <button
                  class="btn btn-secondary rounded-0"
                  type="button"
                  @click="togglePassConfirm"
                >
                  <span v-show="showEyeSlashConfirm === true">
                    <i class="fas fa-eye-slash"></i>
                  </span>
                  <span v-show="showEyeSlashConfirm === false">
                    <i class="fas fa-eye"></i>
                  </span>
                </button>
              </span>
              <input
                type="password"
                id="password-repeat"
                class="form-control"
                v-model="passwordConfirmation"
                dir="ltr"
              />
            </div>
          </div>

          <div v-if="step3" class="mb-3 text-start">
            <label for="password" class="form-label"
              >کد پیامک شده را وارد فرمایید</label
            >

            <div class="input-group" lang="en">
              <input
                type="text"
                id="otp"
                class="form-control"
                v-model="otp"
                dir="ltr"
              />
            </div>
          </div>

          <div class="mb-3 mt-5 text-start">
            <button
              type="button"
              class="btn btn-theme w-100 fw-bold rounded-0"
              @click="userRegister"
            >
              {{ registerText }}
            </button>
          </div>
        </form>

        <!-- Remind Passowrd -->
        <div id="formFooter" class="d-flex justify-content-center mb-3 mt-5">
          <span class="mx-1">قبلا ثبت نام کرده اید؟</span>
          <router-link to="/login" class="text-decoration-none text-warning">
            ورود به حساب
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArefApi from "../libs/api.js";
import utils from '../libs/utils';
export default {
  name: "register",
  created() {
    this.utils= new utils;
    this.arefApi = new ArefApi(this)
  },
  data() {
    return {
      showEyeSlash: true,
      showEyeSlashConfirm: true,
      userPhone: this.$route.query.phone ? this.$route.query.phone : "",
      password: null,
      passwordConfirmation: null,
      referrerFromUrl: this.$route.query.referrer ? true : false,
      referrer: this.$route.query.referrer,
      error: null,
      errorMessage: null,
      step1: true,
      step2: false,
      step3: false,
      registerText: "ادامه",
      first_name: null,
      last_name: null,
      otp: null,
    };
  },
  methods: {
    togglePass: function () {
      this.showEyeSlash = !this.showEyeSlash;
      document.querySelector("#password").type = this.showEyeSlash
        ? "password"
        : "text";
      console.log(this.$route.query);
    },
    togglePassConfirm: function () {
      this.showEyeSlashConfirm = !this.showEyeSlashConfirm;
      document.querySelector("#password-repeat").type = this.showEyeSlashConfirm
        ? "password"
        : "text";
    },
    userRegister: function () {
      if (this.step1) {
        if (!this.validateRegisterStep1()) {
          return;
        }
        this.step2 = true;
        this.step1 = false;
        return;
      }

      if (this.step2) {
        if (!this.validateRegisterStep2()) {
          return;
        }
        const registerData = {
          email: this.userPhone,
          password: this.password,
          password_confirmation: this.passwordConfirmation,
          first_name: this.first_name,
          last_name: this.last_name,
        };
        this.registerText = "کمی صبر کنید ...";
        this.arefApi
          .register(registerData)
          .then((response) => {
            if (response.data) {
              this.step2 = false;
              this.step3 = true;
              this.registerText = "ثبت نام";
            } else {
              this.error =
                "خطا در ثبت اطلاعات کد خطا: " + response.data.message;
            }
          })
          .catch((error) => {
            this.registerText ="ادامه";
            this.error = "خطا در ثبت اطلاعات";
            let tmp = JSON.parse(error.response.data);
            if (tmp.password) {
              this.error = "رمز عبور باید حداقل ۶ کاراکتر باشد";
            }
            if (tmp.first_name) {
              this.error = "نام باید بیش از دو کاراکتر باشد";
            }
          });
      }

      if (this.step3) {
        if (!this.validateRegisterStep3()) {
          return;
        }
        const otpData = {
          email: this.userPhone,
          otp: Number(this.otp),
        };
        this.arefApi
          .verify(otpData)
          .then((response) => {
            this.$store.commit("set", ["token", response.data.access_token]);
            this.$store.commit("set", ["menus", response.data.menus]);
            this.$router.push({ name: "Home" });
          })
          .catch((error) => {
            this.error = "کد وارد شده صحیح نمی باشد";
            console.error("There was an error!", error);
          });
      }
    },
    validateRegisterStep1: function () {
      this.error = null;
      if (!this.first_name) {
        this.error = "نام را وارد نمایید";
        return false;
      }
      if (this.first_name.length < 2) {
        this.error = "نام را صحیح وارد نمایید";
        return false;
      }
      if (!this.last_name) {
        this.error = "نام خانوادگی را وارد نمایید";
        return false;
      }
      if (this.last_name.length < 2) {
        this.error = "نام خانوادگی را صحیح وارد نمایید";
        return false;
      }
      if (!this.userPhone) {
        this.error = "تلفن همراه را وارد نمایید";
        return false;
      }

      if (!this.utils.validMobile(this.userPhone)) {
        this.error = "تلفن همراه را صحیح درج نمایید";
        return false;
      }
      return true;
    },
    validateRegisterStep2: function () {
      this.error = null;
      if (!this.password) {
        this.error = "رمز عبور را وارد نمایید";
        return false;
      }

      if (!this.passwordConfirmation) {
        this.error = "تکرار رمز عبور را وارد نمایید";
        return false;
      }

      if (this.password != this.passwordConfirmation) {
        this.error = "رمز عبور با تکرار آن برابر نمی باشد";
        return false;
      }

      return true;
    },
    validateRegisterStep3: function () {
      this.error = null;
      if (!this.otp) {
        this.error = "لطفا کد پیامک شده را وارد فرمایید";
        return false;
      }
      if (this.otp.length != 4) {
        this.error = "لطفا کد پیامک شده را صحیح وارد فرمایید";
        return false;
      }
      return true;
    },
    checkIsLogged: function () {
      if (this.$store.state.token) {
        this.$router.push({ name: "Home" });
      }
    },
  },
  mounted() {
    this.checkIsLogged();
    document.querySelector("#first_name").focus();
  },
};
</script>
<style scoped>
.login-frm {
  min-width: 300px;
}
</style>